<script>
// vue-intersect polyfill
require('intersection-observer');
// vue-intersect
import Intersect from 'vue-intersect'

import StarRating from 'vue-star-rating'
export default {
    name: 'award',
    components: {
        StarRating,
        Intersect
    },
    data() {
        return {
            rating: 0
        }
    },
    methods: {
        start() {
            if (this.rating < 5) {
                setTimeout(() => {
                    this.rating = this.rating + 0.1
                    this.start(this.rating);
                }, 30);
            }
        },
        reset() {
            this.rating = 0;
        }
    }
}
</script>
